<!-- Description: voice-input-btn -->
<template>
  <div class="voice-input-button" :class="{active: recording, ready: isAudioAvailable}">
    <div class="record-btn"
      v-if="!processing"
      @contextmenu="menuPop"
      @mousedown="start($event)"
      @mouseup="stop($event)"
      @mouseleave="stop($event)"
      @touchstart="start($event)"
      @touchend="stop($event)"
      @touchleave="stop($event)"
    >
      <microphone v-if="!recording" :color="color"></microphone>
      <recording-icon v-else :color="color"></recording-icon>
      <recording-tip v-if="recording" :position="tipPosition"><slot name="recording"></slot></recording-tip>
      <recording-tip v-if="blank" :position="tipPosition"><slot name="no-speak"></slot></recording-tip>
    </div>
    <loading v-else :color="color"></loading>
  </div>
</template>

<script>
import recorder from './recorder'
import { IAT } from './iat-api'
import ASRConfig from './asr-config'
import loading from './components/icons/loading'
import recordingIcon from './components/icons/recording-icon'
import microphone from './components/icons/microphone'
import recordingTip from './components/recording-tip'
export default {
  name: 'voice-input-button',
  components: {
    loading,
    recordingIcon,
    microphone,
    recordingTip
  },
  props: {
    color: {
      type: String,
      default: '#333'
    },
    tipPosition: String,
    server: String,
    appId: String,
    APIKey: String
  },
  data () {
    return {
      recorder,
      processing: false,
      startTime: 0,
      time: 0,
      timer: null,
      recording: false,
      inputTarget: null,
      isAudioAvailable: false,
      blank: false
    }
  },
  methods: {
    menuPop () {
      window.event.returnValue = false
      return false
    },
    start (e) {
      e.preventDefault()
      if (!this.recorder.isAudioAvailable) {
        alert('')
        return
      }
      if (this.recording || (e.which !== 1 && e.which !== 0)) return
      this.recording = true
      this.startTime = new Date().getTime()
      this.timer = setInterval(() => {
        this.time = new Date().getTime() - this.startTime
      }, 20)
      this.recorder.clear()
      this.recorder.start()
      this.$emit('record-start')
    },
    stop (e) {
      e.preventDefault()
      if (!this.recording || (e.which !== 1 && e.which !== 0)) return
      this.recording = false
      this.timer && clearInterval(this.timer)
      this.time = 0
      this.recorder.stop()
      this.$emit('record-stop')
      this.processing = true
      this.recorder.getSource().then(data => {
        this.dictation(data)
      })
    },
    dictation (data) {
      let server = this.server
      let appId = this.appId
      let APIKey = this.APIKey
      IAT(data, {server, appId, APIKey}).then(response => {
        this.processing = false
        let data = response.data.data
        if (data) {
          this.$emit('record', data)
          this.$emit('input', data)
        } else {
          this.$emit('record-blank')
          this.blank = true
          setTimeout(() => {
            this.blank = false
          }, 1000)
        }
      }).catch(error => {
        this.$emit('record-failed', error)
        console.warn(error)
        this.processing = false
      })
    },
    enableAudioButton () {
      this.isAudioAvailable = true
      this.$emit('record-ready')
    }
  },
  created () {
    document.addEventListener('recorder-init', this.enableAudioButton)
  },
  mounted () {
    let {sampleRate, sampleBits} = ASRConfig
    this.recorder.init({sampleRate, sampleBits})
  },
  beforeDestroy () {
    document.removeEventListener('recorder-init', this.enableAudioButton)
    this.recorder.isAudioAvailable && this.recorder.destroy()
    this.recorder = null
  }
}
</script>

<style lang="scss" scoped>
  .voice-input-button{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: .5;
    &.ready{
      opacity: 1;
    }
    .record-btn{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &.disabled{
        pointer-events: none;
        cursor: not-allowed;
      }
      > * {
        pointer-events: none;
      }
    }
  }
  .loader{
    display: inline-block;
    margin: 0 5px;
  }
  .pop{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 40%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,.5);
    pointer-events: none;
    opacity: 0;
    transition: opacity .1s;
    &.recording{
      opacity: 1;
    }
  }
</style>



